.dialogs {
  display: grid;
  grid-template-columns: 1fr 2fr;
  background-color: rosybrown;

  gap: 40px;
}

.dialogsItems {
  padding: 10px;
  /* border: 2px solid green; */
}

.dialog {
  padding: 10px;
  /* border: 2px solid red; */
}

.dialog a {
  color: #fff;
  text-decoration: none;
  cursor: pointer;
}

.dialog a.activeLink {
  color: gold;
}

.messages {
  /* border: 2px solid red; */
}

.avatarWrapper {
  padding: 10px;
}

.avatar {
  width: 40px;
  height: auto;
  border-radius: 50%;
  padding: 10px;
}

.messages {
  display: flex;
  flex-direction: column;
  background-color: rosybrown;

  gap: 40px;
}

